@media (min-width: 768px) {
  .main-nav__item--active {
    position: relative;
    pointer-events: none;
  }
  .main-nav__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    top: 30px;
    left: 0;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item--index::after {
    background: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__item a {
  position: relative;
  display: block;
  text-align: center;
  color: #000000;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .main-nav__item a {
    font-size: 18px;
    font-weight: 400;
    letter-spacing: -0.7px;
  }
  .main-nav__item a:hover {
    opacity: 0.6;
  }
  .main-nav__item a:disabled {
    opacity: 0.3;
  }
}
@media (min-width: 1440px) {
  .main-nav__item a {
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .main-nav__item--index a {
    color: #ffffff;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--opened .main-nav__item {
    display: block;
  }
  .main-nav__item a {
    padding: 21px 20px;
    border-top: 1px solid #e6e6e6;
  }
  .main-nav__item:last-child a {
    border-bottom: 1px solid #e6e6e6;
  }
  .main-nav__toggle {
    z-index: 2;
    display: block;
    width: 56px;
    height: 56px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .main-nav__toggle:hover {
    opacity: 0.6;
  }
  .main-nav__toggle:active {
    opacity: 0.3;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 18px;
    right: 22px;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 29px;
    right: 19px;
    width: 23px;
    height: 2px;
    background-color: #000000;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
}
