@media (min-width: 1440px) {
  .addition__feature {
    width: 300px;
    padding-bottom: 0;
    justify-content: space-between;
  }
  .addition__feature span {
    padding-left: 0;
  }
}
.addition__banner {
  padding: 20px 0;
  position: relative;
}
@media (min-width: 768px) {
  .addition__banner {
    padding: 70px 0;
  }
}
@media (min-width: 1440px) {
  .addition__banner {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .addition__banner-mobile {
    display: none;
  }
}
@media (min-width: 1440px) {
  .addition__banner-mobile {
    display: block;
    height: 266px;
  }
}
.addition__banner-tablet {
  display: none;
}
@media (min-width: 768px) {
  .addition__banner-tablet {
    display: block;
  }
}
@media (min-width: 1440px) {
  .addition__banner-tablet {
    display: none;
  }
}
.addition__banner::before {
  display: block;
  position: absolute;
  content: "";
  width: 78px;
  height: 80px;
  background-image: url("../img/icon/icon_gift.svg");
  top: 100px;
  left: 111px;
}
@media (min-width: 768px) {
  .addition__banner::before {
    top: 125px;
    left: 75px;
  }
}
@media (min-width: 1440px) {
  .addition__banner::before {
    top: 45px;
    left: 75px;
  }
}
.adittion__banner-desc {
  position: absolute;
  margin: 0;
  font-family: Arial;
  font-size: 16px;
  color: #ffffff;
  padding: 0 50px;
  text-align: center;
  top: 220px;
}
@media (min-width: 768px) {
  .adittion__banner-desc {
    left: 380px;
    top: 130px;
    width: 250px;
  }
}
@media (min-width: 1440px) {
  .adittion__banner-desc {
    top: 160px;
    left: -10px;
  }
}
